import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import CircularProgress from "@mui/material/CircularProgress";

const dispatchedEvents = [];

const SrsMuiCircularProgress = function (props, ref) {
  return <CircularProgress {...props} theme={props.muiTheme}/>;
};

const styles = `
  /* Custom styles for the MuiCircularProgress component */
`;

const props = [
  {
    name: "color",
    defaultValue: "primary",
    type: "string",
  },
  {
    name: "size",
    defaultValue: 40,
    type: "number",
  },
  {
    name: "thickness",
    defaultValue: 4,
    type: "number",
  },
  {
    name: "value",
    defaultValue: null,
    type: "number",
  },
  {
    name: "min",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "max",
    defaultValue: 100,
    type: "number",
  },
  {
    name: "disableShrink",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "variant",
    defaultValue: "indeterminate",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to MuiCircularProgress if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiCircularProgress),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
